  .yellow_checkbox[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    width: 15px;
    height: 15px;
    cursor: pointer;
    border-radius: 2px;
    display: flex;
    border: 2px solid #ffa800;
    align-items: center;
    justify-content: center;
  }
  .yellow_checkbox[type="checkbox"]::after {
    content: "\f00c";
    font-weight: 900;
    font-weight: 900;
    font-family: "Font Awesome 5 Free";
    font-size: 9px;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    display: none;
  }
  .yellow_checkbox[type="checkbox"]:checked {
    background-color: #ffa800 !important;
  }
  
  .yellow_checkbox[type="checkbox"]:checked:after {
    display: block;
  }
  
  /*mobile rotation*/
  @media (max-width: 996px) {
    .test_setting {
      grid-template-columns: 1fr;
      grid-template-areas: ".";
    }
  }
  @media (max-width: 500px) {
    .type_of_questions_btn {
      grid-template-columns: 1fr;
      grid-template-areas: ".";
      gap: 5px 0px;
    }
    .all_question_btn {
      border-radius: 5px 5px 5px 5px;
    }
    .with_calulation_btn {
      border-left: 1px solid #ffa800 !important;
      border-right: 1px solid #ffa800 !important;
      border-radius: 5px 5px 5px 5px;
    }
    .without_calulation_btn {
      border-radius: 5px 5px 5px 5px;
    }
  }
  